.steps {
  display: flex;
  justify-content: center;
}

.steps__item .circle {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 0 auto 11px;
  width: 78px;
  height: 78px;
  background: rgba(#DBDBDB, .24);
  border-radius: 50%;
}

.steps__item {
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  color: #B7B4B4;
  position: relative;

  svg {
    position: absolute;
    top: 30%;
    left: 0;
  }

  &--gray {
    color: rgba(#575C6B, .63);

    .circle {
      background: rgba(#DBDBDB, .58);
    }

    svg path {
      fill: rgba(87, 92, 107, 0.636351);
    }
  }

  &--yellow {
    color: #FFC300;

    .circle {
      background: rgba(#FFBF00, .3);
    }

    svg path {
      fill: #FFC300;
    }
  }

  &--orange {
    color: #FF7300;

    .circle {
      background: rgba(#FEAF4D, .53);
    }
  }
}

.steps__item + .steps__item {
  margin-left: 50px;
  padding-left: 50px;
}

@media (max-width: 991px) {
  .steps__item {
    font-size: 16px;
  }

  .steps__item + .steps__item {
    margin-left: 30px;
    padding-left: 30px;
  }
}

@media (max-width: 767px) {
  .steps__item {
    font-size: 14px;

    svg {
      top: 0;
      left: 50%;
      transform: translateX(-50%) rotate(90deg) ;
    }
  }

  .steps {
    flex-direction: column;
  }

  .steps__item + .steps__item {
    margin: 15px 0 0;
    padding: 30px 0 0;
  }
}