*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: $white;

  font-size: 16px;
  font-weight: 400;
  line-height: 1.3;
  color: #575C6B;
  font-family: 'Exo', sans-serif;

  @media (max-width: 767px) {
    font-size: 14px;
  }
}

body.lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}

main {
  margin-top: 76px;

  @media (max-width: 767px) {
    margin-top: 67px;
  }
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn {
  font-family: inherit;
  font-size: inherit;
  padding: 0;
  border: none;
  background: none;
  text-decoration: none;
  cursor: pointer;

  &:focus {
    outline: none;
  }
}

.btn--standard {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #CC2229;
  transition: background-color .2s linear;
  border-radius: 5px;
  color: $white;
  font-weight: 700;

  &:hover {
    background-color: darken(#CC2229, 5%)
  }
}

.input {
  display: block;
  max-width: 100%;
  font-family: inherit;
  font-size: inherit;
  color: inherit;

  background: none;
  border: none;
  border-radius: 0;
  outline: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.title {
  font-size: 36px;
  text-align: center;
  margin-bottom: 50px;

  &--orange {
    color: #FF7700;
  }

  @media (max-width: 1279px) {
    font-size: 30px;
    margin-bottom: 30px;
  }

  @media (max-width: 767px) {
    font-size: 24px;
    margin-bottom: 20px;
  }
}

section {
  margin-bottom: 80px;

  @media (max-width: 767px) {
    margin-bottom: 40px;
  }
}