.section-news {
  background-color:  #F3F3F3;
  padding: 30px 0;

  .title {
    margin-bottom: 30px;
  }
}

.news {
  display: flex;
  justify-content: center;
  align-items: center;

  li {
    margin-right: 50px;

    &:last-child {
      margin-right: 0;
    }
  }
}

@media (max-width: 991px) {
  .news {

    li {
      margin-right: 30px;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}

@media (max-width: 767px) {
  .news {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 30px;

    li {
      margin: 0;
    }
  }
}