.reviews {

  .swiper {
    margin-bottom: 35px;
    width: 100%;
    padding: 0 30px;
  }

  .swiper-button-next,
  .swiper-button-prev {
    width: auto;

    &:after {
      color: $orange;
      font-weight: 700;
      font-size: 26px;
    }
  }

  .swiper-button-next {
    right: 2px;
  }

  .swiper-button-prev {
    left: 2px;
  }

  .swiper-pagination {
    display: none;
  }
}

.reviews__item {
  background: rgba(#D8D8D8, .28);
  border-radius: 7px;
  padding: 23px 25px 16px 34px;

  p {
    font-size: 14px;
  }

  time {
    color: rgba(74, 74, 74, 0.47);
    font-size: 14px;
  }
}

.reviews__header {
  margin-bottom: 16px;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 20px;

  h4 {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 7px;
  }

  & > div {
    display: flex;
    justify-content: space-between;
  }
}

.reviews__rating {
  width: 90px;
  height: 15px;
  background: url("../img/star.svg") repeat-x;
}

.reviews__verified {
  display: flex;
  justify-content: flex-end;
  font-size: 10px;
  color: $orange;
  font-style: italic;

  img {
    margin-right: 4px;
  }
}

.reviews__text {
  text-align: center;
  font-weight: 700;
  color: $orange;
}

.reviews__footer {
  time {
    display: none;
  }
}

@media (max-width: 991px) {
  .reviews__item {
    padding: 15px;

    p {
      margin-bottom: 15px;
    }
  }

  .reviews__header {

    time {
      display: none;
    }
  }

  .reviews__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;

    time {
      display: block;
    }
  }
}

@media (max-width: 767px) {
  .reviews {

    .swiper {
      padding: 0 0 35px;
      margin: 0;
    }

    .swiper-button-prev,
    .swiper-button-next {
      display: none;
    }

    .swiper-pagination {
      display: block;
      bottom: 0;
    }

    .swiper-pagination-bullet {
      background-color: #C4C4C4;
      opacity: 1;
    }

    .swiper-pagination-bullet-active {
      background-color: #FF6D00;
    }
  }

  .reviews__text {
    display: none;
  }
}