.section-intro {
  position: relative;
  overflow-x: hidden;

  &:after {
    content: '';
    display: block;
    width: 963px;
    height: 569px;
    background: url("../img/rectangle.svg") no-repeat center;
    background-size: contain;

    position: absolute;
    bottom: 10%;
    right: -10%;
    z-index: -2;
    user-select: none;
    pointer-events: none;
  }
}

.intro__inner {
  display: flex;
  align-items: center;
}

.intro__content {
  margin-top: 84px;
}

.intro__img {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
  user-select: none;
  pointer-events: none;
}

.intro__title {
  margin-bottom: 40px;
  font-size: 54px;
  max-width: 764px;
}

.intro__list {
  margin-bottom: 55px;
  max-width: 425px;

  li {
    margin-bottom: 24px;
    display: flex;
    align-items: flex-start;
    font-size: 18px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  img {
    margin-right: 44px;
  }
}

.intro__form-wrapper {
  color: $white;
  max-width: 1050px;
  margin: 0 auto;
}

.intro__form {
  background: linear-gradient(317.76deg, #FFA600 40.02%, #FF7800 68.68%, #FF6D00 83.15%);
  border-radius: 10px;
  padding: 40px 40px 16px;
  display: grid;
  grid-template-columns: 1fr 477px;
  grid-gap: 65px;

  .input {
    font-size: 12px;
    height: 47px;
    width: 100%;
    background-color: $white;
    padding: 0 15px;
    border-radius: 5px;
    color: #575C6B;
    border: 1px solid $white;
    transition: border-color .2s linear;

    &:hover,
    &:focus {
      border-color: #575C6B;
    }

    &::placeholder {
      color: #575C6B;
    }
  }

  fieldset {
    border: none;
    padding: 0;
    margin: 0;

    &:first-child {
      margin-bottom: 24px;
    }
  }

  legend {
    padding: 0;
    font-size: 18px;
    margin-bottom: 16px;
  }

  .btn {
    margin-bottom: 6px;
    font-size: 18px;
    width: 100%;
    height: 47px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
    position: relative;

    &:hover img {
      transform: translateX(5px);
    }

    img {
      position: absolute;
      right: 30px;
      transition: transform .2s linear;
    }
  }
}

.intro__form-header {
  display: flex;
  align-items: center;

  h3 {
    margin-right: 45px;
    position: relative;
    z-index: -1;
    margin-bottom: -20px;
    border-radius: 5px;
    font-size: 22px;
    padding: 20px 20px 20px 40px;
    background: linear-gradient(317.76deg, #FFA600 -31.52%, #FF7800 -2.87%, #FF6D00 11.61%);
  }

  img {
    user-select: none;
    pointer-events: none;
    margin-bottom: -6px;
  }
}

.intro__form-inner > p {
  font-size: 11px;
  text-align: center;
}

.intro__inputs-wrapper {
  margin-bottom: 15px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 16px;

  .intro__inputs:last-child input:first-child {
    margin-bottom: 16px;
  }
}

.intro__select {
  margin-bottom: 16px;
  padding: 0 15px;
  width: 100%;
  height: 47px;
  outline: none;
  border: 1px solid $white;
  transition: border-color .2s linear;
  border-radius: 5px;

  font-size: 12px;
  font-family: inherit;
  color: #575C6B;
  -moz-appearance:none; /* Firefox */
  -webkit-appearance:none; /* Safari and Chrome */
  appearance:none;
  cursor: pointer;

  background-color: white;
  background-image: url('../img/select-arrow-icon.svg');
  background-repeat: no-repeat;
  background-position-x: 95%;
  background-position-y: 50%;

  &:hover,
  &:focus {
    border-color: #575C6B;
  }
}

// custom radio
.custom-radio {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.custom-radio+label {
  margin-right: 50px;
  display: inline-flex;
  align-items: center;
  user-select: none;
  cursor: pointer;

  &:last-child {
    margin-right: 0;
  }
}

.custom-radio+label::before {
  content: '';
  display: inline-block;
  width: 22px;
  height: 22px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #C9020F;
  background-color: $white;
  border-radius: 50%;
  margin-right: 10px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}

.custom-radio:checked+label::before {
  background-image: url("../img/radio-checked.svg");
}

@media (max-width: 1279px) {
  .intro__img {
    width: 450px;
  }

  .section-intro {

    &:after {
      bottom: 20%;
      right: -20%;
      background-size: 70%;
    }
  }

  .intro__title {
    font-size: 40px;
  }

  .intro__content {
    margin-top: 60px;
  }
}

@media (max-width: 991px) {

  .section-intro {

    &:after {
      bottom: auto;
      top: 0;
      right: -35%;
      background-size: 50%;
    }
  }

  .intro__img {
    width: 350px;
  }

  .intro__title {
    font-size: 30px;
    max-width: 400px;
  }

  .intro__list {
    margin-bottom: 30px;
  }

  .intro__list li {
    font-size: 16px;
  }

  .intro__list li img {
    margin-right: 15px;
  }

  .intro__form-header {
    flex-direction: column;
    align-items: flex-start;

    h3 {
      position: static;
      margin: 0;
      padding: 0 15px;
      background: none;
      z-index: 0;
    }

    img {
      order: -1;
      margin: 0 0 8px;
      z-index: 1;
    }
  }

  .intro__form {
    padding: 20px 15px 30px;
    background: none;
    border: none;
    grid-template-columns: 1fr;
    grid-gap: 16px;
  }

  .intro__form-wrapper {
    position: relative;
    background: linear-gradient(333.24deg, #FAD961 -52.95%, #FF6E00 93.1%);
    border-radius: 10px;
  }
}

@media (max-width: 767px) {
  .section-intro {

    &:after {
      top: -10%;
      right: -55%;
      background-size: 40%;
    }
  }

  .intro__title {
    font-size: 24px;
    max-width: 184px;
  }

  .intro__img {
    width: 250px;
  }

  .intro__list li {
    font-size: 14px;
  }

  .intro__form {

    legend {
      font-size: 14px;
    }

    fieldset {
      margin-bottom: 16px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .intro__inputs-wrapper {
    margin-bottom: 0;
    grid-template-columns: 1fr;
  }

  .intro__inputs:last-child {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 11px;
  }
}

@media (max-width: 575px) {
  .intro__img {
    width: 200px;
  }

  .section-intro {

    &:after {
      top: -12%;
      right: -100%;
    }
  }
}