.footer {
  background-color: #9095A4;
  padding: 20px 0 30px;
  color: $white;

  .nav__link:hover {
    color: $white;
  }
}

.footer__top {
  margin-bottom: 22px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__right {
  display: flex;
  align-items: center;
}

.footer__contacts {
  margin-bottom: 35px;
  display: flex;

  address {
    margin-right: 90px;
    font-style: normal;
    max-width: 390px;
    font-size: 12px;

    span {
      font-weight: 500;
    }
  }

  a {
    display: block;
    font-size: 12px;
    font-weight: 500;
  }

  time {
    font-size: 12px;
  }
}

.footer__lists {
  padding-bottom: 18px;
  margin-bottom: 12px;
  border-bottom: 1px solid $white;
  display: grid;
  grid-template-columns: repeat(7, 1fr);

  a {
    font-size: 12px;
  }
}

.footer__copy {
  font-size: 12px;

  a {
    display: block;
    margin-bottom: 11px;
  }
}

@media (max-width: 991px) {
  .footer__right {
    display: none;
  }

  .footer__lists {
    grid-template-columns: repeat(4, 1fr);
  }

  .footer__contacts address {
    margin-right: 45px;
  }
}

@media (max-width: 767px) {
  .footer__top {
    padding-bottom: 11px;
    border-bottom: 1px solid $white;
    margin-bottom: 19px;
  }

  .footer__contacts {
    padding-bottom: 21px;
    margin-bottom: 17px;
    border-bottom: 1px solid $white;
    display: block;

    address {
      margin: 0 0 16px;
    }
  }

  .footer__lists {
    margin-bottom: 15px;
    padding-bottom: 0;
    border: none;
  }
}

@media (max-width: 575px) {
  .footer__lists {
    grid-template-columns: 1fr 1fr;
  }
}