.benefits {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-gap: 40px;
  position: relative;

  &:before {
    content: '';
    display: block;
    width: 589px;
    height: 519px;
    background: url("../img/benefits-rectangle.svg") no-repeat center;
    background-size: contain;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    z-index: -2;
  }

  h4 {
    color: $orange;
    font-size: 24px;
    margin-bottom: 16px;
  }

  ul li {
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.benefits__img {
  position: relative;
  z-index: -1;
  margin-top: -145px;
}

@media (max-width: 1279px) {
  .benefits {
    grid-gap: 0;
  }

  .benefits__img {
    margin-top: -80px;
    width: 500px;
  }
}

@media (max-width: 991px) {
  .benefits__img {
    margin: 0;
    width: 400px;
  }

  .benefits {

    h4 {
      font-size: 22px;
    }

    &:before {
      background-size: 75%;
      left: -10%;
      top: -5%;
      transform: none;
    }
  }
}

@media (max-width: 767px) {
  .section-benefits {
    overflow-x: hidden;
  }

  .benefits {
    grid-template-columns: 1fr;
    grid-gap: 30px;

    h4 {
      font-size: 18px;
    }

    li {
      margin-bottom: 20px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &:before {
      background-size: 75%;
      left: -20%;
      top: -5%;
      transform: none;
    }
  }
}

@media (max-width: 576px) {
  .benefits {

    &:before {
      background-size: 60%;
      left: -50%;
      top: -5%;
    }
  }

  .benefits__img {
    margin-left: -15px;
  }
}