.section-services {
  padding: 50px 0 36px;
  background-color: #F5F5F5;

  .title {
    margin-bottom: 10px;
  }

  .text-main {
    text-align: center;
    margin: 0 auto 35px;
    max-width: 820px;
  }
}

.services {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;

  li {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-gap: 15px;
  }
}

@media (max-width: 1279px) {
  .services {
    grid-gap: 30px;
  }
}

@media (max-width: 991px) {
  .services {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 767px) {
  .services {
    h4 {
      display: none;
    }
  }
}

@media (max-width: 575px) {
  .services {
    grid-template-columns: 1fr;
    grid-gap: 20px;

    img {
      margin: 0 auto;
    }

    li {
      grid-template-columns: 85px 1fr;
      grid-gap: 30px;
    }
  }
}