.nav {
  margin-right: 40px;
}

.nav__list {
  display: flex;
}

.nav__item + .nav__item {
  margin-left: 50px;
}

.nav__link {
  font-size: 10px;
  transition: color .2s linear;

  &:hover {
    color: darken(#575C6B, 5%);
  }
}

@media (max-width: 991px) {
  .nav {
    margin: 0 0 30px;
  }

  .nav__list {
    display: block;
  }

  .nav__item + .nav__item {
    margin: 20px 0 0;
  }
}