.header {
  background: $white;
  box-shadow: 0 4px 15px rgba(0, 0, 0, .1);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

.header__inner {
  padding: 15px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__inner > .btn--standard {
  display: none;
}

.header__right {
  display: flex;
  align-items: center;
}

.header__action {
  display: flex;
  align-items: center;

  .btn--standard {
    font-size: 12px;
    height: 37px;
    padding: 0 28px;
  }

  li:last-child {
    margin-left: 35px;
  }

  li:last-child a {
    font-size: 10px;
    font-weight: 700;
    text-decoration: underline;
  }
}

@media (max-width: 991px) {
  .header__right {
    padding: 30px;
    position: fixed;
    top: 76px;
    left: 0;
    width: 100%;
    height: calc(100% - 76px);
    z-index: 9;
    overflow: hidden;
    overflow-y: auto;
    transform: translateX(-100%);
    transition: .3s ease-out;
    background: #fff;
    display: block;
  }

  .header__right.active {
    transform: translateX(0);
  }

  .header__logo-wrapper {
    display: flex;
    align-items: center;
  }

  .header__inner > .btn--standard {
    display: flex;
    height: 30px;
    font-size: 10px;
    background: linear-gradient(179.04deg, #CC2229 -29.81%, #9F041B 129.89%);
    border-radius: 5px;
    padding: 0 16px;
  }
}

@media (max-width: 767px) {
  .header__logo {
    width: 118px;
    height: 37px;
  }

  .header__right {
    top: 67px;
    height: calc(100% - 67px);
  }
}